import React, { useEffect, useState } from "react";
import styled from "styled-components";

export function About() {
  var i = 0;
  var txt =
    " is a web developer,film and augmented reality artist. Who is interested in the internet as an emerging social space. ";

  /* The text */
  var speed = 80; /* The speed/duration of the effect in milliseconds */

  function typeWriter() {
    if (i < txt.length && document.getElementById("bio")) {
      document.getElementById("bio").innerHTML += txt.charAt(i);
      i++;
      setTimeout(typeWriter, speed);
      document.getElementById("me").style.bottom = "0";
    } else {
      return null;
    }
  }

  const About = styled.p`
    font-family: monospace !important;
    position: relative;
    left: 1vw;
    font-size: 4.4vw;
    color: blue;

    width: 80vw;
    height: 40vw;
    padding: 1em;
    margin: 0;

    @media all and (max-width: 500px) {
      font-size: 6vw;
      padding: 0em;
    }
  `;
  const Me = styled.img`
    position: absolute;
    bottom: -40%;
    right: 5%;
    height: 40%;
    transition: ease-out 0.4s;

    &:hover {
      filter: opacity(10%);
    }
  `;
  let audioEl = new Audio("./assets/music/sound-on.mp4");
  const Playit = () => {
    audioEl.play();
  };
  useEffect(() => {
    typeWriter();
    Playit();
  }, 100);

  return (
    <div>
      <audio className="audio-element">
        <source
          src={process.env.PUBLIC_URL + "/assets/music/sound-on.mp4"}
        ></source>
      </audio>
      <About id="bio">
        Michael Elijah <hr />
      </About>
      <Me id="me" src={process.env.PUBLIC_URL + "/assets/img/me.png"} />
    </div>
  );
}
